.body {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 12px;
  background-color: #e2dae2;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px;
  background-color: #f4f1ea;
  box-shadow: 0px 1px 8px #c0bfbd;
}

.logo {
  color: #776b4d;
  text-shadow: 2px 2px 4px #5c584e;
  font-size: 25px;
  line-height: 15px;
}

.navlinks a {
  justify-content: space-around;
  align-items: center;
  color: #5c584e;
  margin: 5px;
  padding: 10px;
  text-decoration: none;
  font-size: 20px;
  height: 20px;
}

.searchBox {
  display: flex;
  justify-content: center;
  height: 4vw;
  margin-top: 10px;
  padding: 10px;
}

.searchBox,
input button {
  height: 3vw;
}

.searchBox input {
  width: 30vw;
  padding: 5px;
  border: 2px solid #b8b6b3;
}

.searchBox button {
  margin-left: 10px;
  padding: 10px;
  background-color: #f4f1ea;
  border: none;
  border-radius: 2px;
}

.gatorGif {
  display: flex;
  justify-content: center;
  padding-top: 5vw;
}

.books {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 5px;
  padding: 20px;
}

.booksDisplay {
  flex-basis: 25%;
  border: 1px solid #b8b6b3;
  padding: 10px;
}

.booksDisplay h4 {
  text-transform: uppercases;
  margin-bottom: 0;
}

.publishDate {
  display: inline;
}

.subjects {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subjects ul {
  list-style-type: none;
}

.subjects a {
  text-decoration: none;
}

.authorPage {
  display: flex;
  justify-content: center;
  margin: 0;
}
